import React from 'react';
import {FaThumbsUp, FaMap} from 'react-icons/fa'  
import {db} from '../firebase'
import './list.css'
import {Link} from 'react-router-dom'
var length;

  class ClassList extends React.Component{
    state = {
        classes:null,
        visible:false,
        name:"null",
      }

      componentDidMount(){
        const data = db.collection('Classes');    
        const classes = []
        const lat = []
         data.get()
          .then(snapshot=>{
            length = snapshot.size;
            snapshot.forEach(doc=>{
              const data = doc.data()
              classes.push(data)
            })
            this.setState({classes:classes})
          })
         
      }
     
      render(){
          return(
              <div>
                <div style={{backgroundColor:'#E6E6E6'}}>
                    <div class='number' ><FaThumbsUp size='12' style={{marginRight:'5px'}}/> Found {length} Pidgin classes around you.</div>
                    {       
                              this.state.classes&&
                              this.state.classes.map(classes=>{
                                return(
                                  <Link to={{
                                    pathname:'/classDisplay',
                                    state:{
                                      docName: classes.docName,
                                      name: classes.name,
                                      location: classes.location,
                                    }
                                  }}>
                                    <div class='item'>
                                      <div class='showImage'>
                                        <img src={classes.i1} height='200px' style={{marginRight:'2px'}}></img>
                                        <img src={classes.i2} height='200px' style={{marginRight:'2px'}}></img>
                                        <img src={classes.i3} height='200px' style={{marginRight:'2px'}}></img>
                                      </div>

                                        <div class='container'>
                                        <div class='name'>{classes.name}</div>
                                          <div class='map'>
                                            <a href={classes.location}><FaMap size='13'color='#04BFBF' /></a>
                                            <div>Map</div>
                                            </div>
                                        </div>
                                        
                                          <div class='type'>
                                            {classes.type}
                                          </div>
                                          <div class='type'>
                                            {classes.adress}
                                          </div>
                                          <hr color='#E6E6E6'></hr>
                                          <div class='fees'>
                                            <div class='type'>Starting Fees  &#8377;{classes.fees}</div>
                                          </div> 
                                    </div>
                                  </Link>
                                )
                          })
                        }
                  </div>
            </div>
          )
      }
  }
  export default ClassList;