import React from 'react'
import {FaSearch,FaFilter} from 'react-icons/fa';
import './AppBar.css'

class MyAppBar extends React.Component{
    render() {
        return (
          <div class='title'>
            <div class='orange'>
                <div style={{marginBottom:'20px',fontSize:'20px'}}>
                    CLASSES
                </div>
                <div class='searchBar'>
                    <div styl={{marginRight:'4px'}}><FaSearch size='15' color='#043540' /></div>
                    <div class='hint'>Search music, art, gyms....</div>
                </div>
            </div>

            <div style={{display:'flex',marginTop:'7px'}} class='filterRow'>
                <div class='filters'>
                    <FaFilter size='12' color='rgb(4, 53, 64)' style={{marginTop:'3px',marginRight:'2px'}}/>
                    <div class='filtertext'>Filters</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Age</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Fees</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Duration</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Pidgin Picks</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Online</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>For Kids</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Sports</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Only For Women</div>
                </div>
                <div class='filters'>
                    <div class='filtertext'>Fitness</div>
                </div>
            </div>
            <hr color='#E6E6E6'></hr>
          </div>
        )
    }
}
export default MyAppBar;