import React, { useState } from 'react'
import {db} from '../firebase'
import './topPicks.css'
import ReactAvatar from 'react-avatar';
import {FaStar } from 'react-icons/fa';

class TopPicks extends React.Component{
    state = {
        images:null
      }

      componentDidMount(){
        const data = db.collection('ImagesClassesTopPicks');    
         data.get()
          .then(snapshot=>{
            const images = []
            snapshot.forEach(doc=>{
              const data = doc.data()
              images.push(data)
            })
            this.setState({images:images})
          })
      }
    
    
    render(){
        return(
            <div>
                <div class='topPicks'>
                    <div style={{paddingTop:'2px',marginRight:'5px'}}>
                        <FaStar color='#043540' style={{marginTop:'0px'}}/>
                        </div>
                    Top Picks
                </div>
                <div class='containerTop'>
                {
                          this.state.images&&
                          this.state.images.map(images=>{
                            return(
                                <div class='avatarTop'>
                                    <a href={images.url}>
                                        <ReactAvatar src={images.image} size='90' class='avatar' round='50%' class='avatar'/>
                                    </a>
                                </div>
                            )
                          })
                }
                </div>
                <hr style={{margin:'0px'}} size='7' color="#E6E6E6"></hr>
            </div>
        )
    }
}
export default TopPicks;